.NavigationItem{
    margin:10px 0;
    width:100%;
    display:block;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.NavigationItem a{
    color: #8F5C2C;
    text-decoration: none;
    width:100%;
    display: block;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active{
    color: #40A4C8;
}

@media (min-width: 500px){
    .NavigationItem{
        margin:0;
        height:100%;
        width:auto;
        display:-ms-flexbox;
        display:flex;
        -ms-flex-align: center;
            align-items: center;
    }
    .NavigationItem a{
        color:white;
        height: 100%;
        width:auto;
        padding: 16px 10px;
        border-bottom: 4px solid transparent;
    }
    .NavigationItem a:hover,
    .NavigationItem a:active,
    .NavigationItem a.active
   {
        background-color: #8F5C2C;
        border-bottom: 4px solid #40A4C8;
        color: white;
    }
}




